/* FONT FACE */
.OrganizationUser-stats {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 940px;
  padding: 30px 0;
  border-bottom: 1px solid #dddddd; }

.OrganizationUser-stat {
  width: 120px;
  text-align: center; }

.OrganizationUser-statIcon {
  width: 38px;
  height: 38px;
  border: 1px solid #dddddd;
  border-radius: 4px;
  line-height: 40px;
  text-align: center; }

.OrganizationUser-statInfo {
  display: block;
  padding: 10px 0 0;
  text-align: center; }

.OrganizationUser-statTitle {
  display: block;
  color: #333333;
  font-size: 15px;
  font-style: normal;
  font-weight: 500; }

.OrganizationUser-statLabel {
  color: #999999;
  font-size: 13px; }

.OrganizationUser-statIcon {
  color: #D1D1D1;
  font-size: 18px; }

.CDB-IconFont-eye.OrganizationUser-statIcon {
  font-size: 24px;
  line-height: 29px; }

.CDB-IconFont-marker.OrganizationUser-statIcon {
  font-size: 22px; }

.CDB-IconFont-progressBar.OrganizationUser-statIcon {
  font-size: 24px;
  line-height: 24px; }

.CDB-IconFont-twitter.OrganizationUser-statIcon {
  font-size: 22px;
  line-height: 32px; }
