/* FONT FACE */
/**
  *  Progress element styles
  *
  *  It needs:
  *  - Height of the progress "bar".
  *  - Border radius of the element.
  *  - If it needs a white gradient over it.
  *  - If bars needs a min width.
  *
  */
/* FONT FACE */
.OrganizationList {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
  margin-top: 1px;
  padding-bottom: 30px; }

.OrganizationList--centered {
  align-items: center;
  justify-content: center;
  min-height: 300px; }

.OrganizationList-user {
  position: relative;
  width: 100%;
  padding: 0 19px;
  border: 1px transparent solid;
  border-radius: 4px; }
  .OrganizationList-user:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.06); }

.OrganizationList-user.is-selectable:hover {
  border-color: #bcd8f1;
  background-color: #f5fafe; }

.OrganizationList-user.is-selected {
  border-color: #9dc2e0;
  background-color: #eff6fc; }
  .OrganizationList-user.is-selected:hover {
    border-color: #9dc2e0;
    background-color: #eff6fc; }

.OrganizationList-userAvatar {
  width: 42px;
  height: 42px;
  margin-right: 20px; }

.OrganizationList-userAvatar--img {
  border: 0; }

.OrganizationList-userLink {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  padding: 20px 0;
  text-decoration: none !important; }

.OrganizationList-userInfo {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  width: 100%; }

.OrganizationList-userInfoName {
  display: flex;
  flex-direction: column;
  align-content: space-between;
  justify-content: space-between;
  max-width: 350px;
  height: 40px;
  padding: 1px 0; }

.OrganizationList-userInfoSubtitle {
  color: #999999;
  font-size: 13px;
  font-weight: 300;
  line-height: 16px; }

.OrganizationList-userInfoData {
  display: flex;
  flex-direction: row; }
  .OrganizationList-userInfoData--paragraph-icon {
    margin-right: 4px; }

.OrganizationList-userStats {
  width: 200px;
  margin-top: 10px; }
  .OrganizationList-userStats div.progress-bar {
    display: block;
    position: relative;
    width: 100%;
    height: 6px;
    overflow: hidden;
    border-radius: 20px;
    background: #F5F5F5; }
    .OrganizationList-userStats div.progress-bar.overflow-visible {
      overflow: visible; }
    .OrganizationList-userStats div.progress-bar .bar-3,
    .OrganizationList-userStats div.progress-bar .bar-2,
    .OrganizationList-userStats div.progress-bar .bar-1,
    .OrganizationList-userStats div.progress-bar .bar-0 {
      position: absolute;
      top: 0;
      bottom: 0;
      border-radius: 20px;
      min-width: 4px; }
    .OrganizationList-userStats div.progress-bar .bar-3 {
      z-index: 3;
      border-right: 1px solid #FFF;
      background: #7DD4FF; }
    .OrganizationList-userStats div.progress-bar .bar-2 {
      z-index: 2;
      background: #0090D7; }
    .OrganizationList-userStats div.progress-bar .bar-1 {
      z-index: 1;
      background: #E5E5E5; }
      .OrganizationList-userStats div.progress-bar .bar-1::after {
        display: none; }
      .OrganizationList-userStats div.progress-bar .bar-1::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        border-right: 1px solid white; }
    .OrganizationList-userStats div.progress-bar .bar-0 {
      background: url("/assets/1.0.0-assets.136/images/layout/unassigned_bkg.png") repeat-x left 0;
      z-index: 0; }
      .OrganizationList-userStats div.progress-bar .bar-0::after {
        display: none; }
    .OrganizationList-userStats div.progress-bar .danger {
      background: #f15743; }
    .OrganizationList-userStats div.progress-bar .caution {
      background: #f8b85c; }

.OrganizationList-userLink:hover {
  cursor: pointer; }
  .OrganizationList-userLink:hover:not(.is-disabled) .OrganizationList-userInfoName {
    color: #227dbd; }

.OrganizationList-userLink.is-disabled {
  opacity: 0.5;
  cursor: initial; }

.OrganizationList-paginator {
  display: flex;
  justify-content: flex-end;
  padding: 30px 0; }
