/* FONT FACE */
.OrganizationSearch {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: -16px;
  padding: 16px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1); }

.OrganizationSearch-Form {
  position: relative; }

.OrganizationSearch-Item {
  line-height: 30px; }
  .OrganizationSearch-Item.RoleCount {
    color: #979EA1;
    white-space: nowrap; }

.OrganizationSearch-FormButton {
  padding-right: 2px;
  border: none;
  outline: none;
  background: transparent;
  color: #333333;
  font-size: 14px; }
  .OrganizationSearch-FormButton::after {
    content: '';
    display: inline-block;
    position: relative;
    top: 3px;
    left: 20px;
    width: 1px;
    height: 20px;
    background: rgba(0, 0, 0, 0.06);
    vertical-align: text-bottom; }
  .OrganizationSearch-FormButton > i {
    display: inline;
    margin-right: 10px;
    margin-left: 1px;
    font-weight: bold;
    vertical-align: middle; }

.OrganizationSearch-FormButton--clean {
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -12px;
  border-radius: 100%;
  outline: none;
  background: transparent;
  color: #bbbbbb;
  text-align: center; }
  .OrganizationSearch-FormButton--clean:hover {
    color: #888888; }

.OrganizationSearch-FormInput {
  width: 220px;
  padding-left: 5px;
  border: none;
  outline: none;
  background: none;
  font-size: 15px;
  font-weight: 300; }
  .OrganizationSearch-FormInput::-webkit-input-placeholder {
    color: #DDD;
    font-style: italic;
    font-weight: 500; }
  .OrganizationSearch-FormInput::-moz-placeholder {
    color: #DDD;
    font-style: italic;
    font-weight: 500; }
  .OrganizationSearch-FormInput:-ms-placeholder {
    color: #DDD;
    font-style: italic;
    font-weight: 500; }
