/* FONT FACE */
/**
  *  Progress element styles
  *
  *  It needs:
  *  - Height of the progress "bar".
  *  - Border radius of the element.
  *  - If it needs a white gradient over it.
  *  - If bars needs a min width.
  *
  */
/* FONT FACE */
.OrganizationUser-quota {
  display: flex; }

.OrganizationUser-quotaProgress {
  display: block;
  width: 300px; }
  .OrganizationUser-quotaProgress div.progress-bar {
    display: block;
    position: relative;
    width: 100%;
    height: 10px;
    overflow: hidden;
    border-radius: 20px;
    background: #F5F5F5; }
    .OrganizationUser-quotaProgress div.progress-bar.overflow-visible {
      overflow: visible; }
    .OrganizationUser-quotaProgress div.progress-bar .bar-3,
    .OrganizationUser-quotaProgress div.progress-bar .bar-2,
    .OrganizationUser-quotaProgress div.progress-bar .bar-1,
    .OrganizationUser-quotaProgress div.progress-bar .bar-0 {
      position: absolute;
      top: 0;
      bottom: 0;
      border-radius: 20px;
      min-width: 3px; }
    .OrganizationUser-quotaProgress div.progress-bar .bar-3 {
      z-index: 3;
      border-right: 1px solid #FFF;
      background: #7DD4FF; }
    .OrganizationUser-quotaProgress div.progress-bar .bar-2 {
      z-index: 2;
      background: #0090D7; }
    .OrganizationUser-quotaProgress div.progress-bar .bar-1 {
      z-index: 1;
      background: #E5E5E5; }
      .OrganizationUser-quotaProgress div.progress-bar .bar-1::after {
        display: none; }
      .OrganizationUser-quotaProgress div.progress-bar .bar-1::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        border-right: 1px solid white; }
    .OrganizationUser-quotaProgress div.progress-bar .bar-0 {
      background: url("/assets/1.0.0-assets.136/images/layout/unassigned_bkg.png") repeat-x left 0;
      z-index: 0; }
      .OrganizationUser-quotaProgress div.progress-bar .bar-0::after {
        display: none; }
    .OrganizationUser-quotaProgress div.progress-bar .danger {
      background: #f15743; }
    .OrganizationUser-quotaProgress div.progress-bar .caution {
      background: #f8b85c; }

.OrganizationUser-quotaBar {
  display: block;
  width: 300px;
  overflow: visible; }

.OrganizationUser-quotaLegend {
  display: flex;
  align-items: center;
  margin: 10px 0 0; }

.OrganizationUser-quotaLegendItem {
  margin: 0 12px 0 0;
  color: #999999;
  font-size: 13px; }

.OrganizationUser-quotaLegendIcon {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 4px;
  border-radius: 10px;
  background: #F5F5F5;
  box-shadow: inset 0 0 1px 0 rgba(0, 0, 0, 0.25); }

.OrganizationUser-quotaLegendIcon--assigned {
  background: #1181FB; }

.OrganizationUser-quotaLegendIcon--used {
  background: #71CEFB; }

.OrganizationUser-quotaInfo {
  display: inline-block;
  color: #999999;
  font-size: 13px;
  font-weight: 300;
  line-height: 13px; }

.OrganizationUser-quotaInfoInput {
  box-sizing: border-box;
  width: 70px !important;
  height: 32px;
  padding: 0 5px;
  text-align: right; }

.OrganizationUser-quotaInfoLink {
  color: #3aa9e3; }
  .OrganizationUser-quotaInfoLink:hover {
    color: #227dbd;
    text-decoration: underline; }

.UserQuotaDropdown {
  width: 220px;
  padding: 20px; }

.UserQuotaDropdown-title {
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
  color: #333333;
  font-size: 15px; }

.UserQuotaDropdown-info {
  display: block;
  margin-top: 15px;
  color: #999999;
  font-size: 13px; }

.UserQuotaDropdown-slider {
  width: 220px;
  margin-top: 12px; }
