.Modal-listActions {
  margin-top: 32px; }

.Modal-listActionsitem,
.Modal-icon {
  margin-right: 16px; }

.Modal-listActionsitem:last-child {
  margin-right: 0; }

.Modal-icon {
  margin-top: 6px;
  line-height: 34px;
  /* align-items: baseline doesnt work correctly in ff */ }

.Modal-icon--svg {
  width: 25px;
  height: 25px; }

.Modal-listTextItem {
  margin-top: 32px; }

.Modal-listTextHighlight {
  display: inline-block;
  padding: 4px 5px;
  border-radius: 2px;
  background: #F9F9F9; }
  .Modal-listTextHighlight.is-code {
    font: 500 12px 'Monaco', 'Monospace'; }

.Modal-listForm {
  margin-left: 24px; }

.Modal-listFormItem {
  margin-right: 32px; }
  .Modal-listFormItem:last-child {
    margin-right: 0; }
  .Modal-listFormItem.is-disabled {
    color: #CBCED0; }

.Modal-addItem {
  width: 40px;
  height: 40px;
  border: 1px solid #DDD;
  border-radius: 4px; }

.Modal-titleBasemap {
  margin: 24px 0; }

/* animation analysis */
.Analysis-animation {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 304px;
  max-width: 100%;
  height: 96px;
  overflow: hidden;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  background: #2E3C43; }
  .Analysis-animation.is-rounded {
    border-radius: 4px; }

.Analysis-info {
  padding: 16px; }

.Analysis-link {
  display: inline-flex;
  font-weight: 600; }

.ModalBlockList-item.is-disabled {
  border: none;
  opacity: 0.24;
  pointer-events: none; }

.ModalBlockList-item.is-disabled .Analysis-animation {
  background-color: transparent;
  filter: grayscale(100); }

.ModalBlockList-item.is-disabled .u-actionTextColor {
  display: none; }

.ModalBlockList-item-body {
  display: block; }

.SubmenuModal {
  margin-bottom: 32px;
  padding-bottom: 24px; }

.SubmenuModal-item::after {
  content: '';
  width: 3px;
  height: 6px;
  margin-left: 12px;
  background-image: url(data:image/svg+xml;base64,); }

.SubmenuModal-item:last-child::after {
  display: none; }
