.InviteUsers {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%; }

.InviteUsers-form {
  width: 800px; }

.InviteUsers-formContent,
.InviteUsers-formFooter {
  width: 800px;
  margin: 0 auto; }

.InviteUsers-formContent {
  position: relative;
  padding: 28px 0 120px; }

.InviteUsers-formFooter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 800px;
  border-top: 1px solid #dddddd;
  background: #f9f9f9; }

.Form-rowData.InviteUsers-userType {
  justify-content: flex-start; }
  .Form-rowData.InviteUsers-userType .Form-label {
    margin-left: 10px; }

.InviteUsers-formFooterSticky {
  position: fixed;
  z-index: 10;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 97px;
  background-color: #f9f9f9; }

.InviteUsers .Form-rowInfoText--error {
  width: 140px; }
