.AssetsList-recent {
  overflow: hidden; }

.AssetsList-recentInner {
  display: flex;
  width: 1000px; }

.AssetsList {
  display: flex;
  position: relative; }

.AssetListItem.Carousel-item {
  width: 40px;
  min-width: 40px;
  height: 40px; }

.AssetsList-inner {
  display: block;
  position: relative;
  height: auto;
  max-height: none;
  margin: 35px 0 70px;
  padding: 0 0 0 5px;
  overflow: auto; }

.AssetItem-button {
  width: 100%;
  height: 100%; }

.AssetItem-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%; }

.AssetItem-label {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%; }

.Modal-inner .AssetsList {
  flex-wrap: wrap;
  margin: 0 -14px 20px; }

.Modal-inner .AssetsList:last-child {
  flex-wrap: wrap;
  margin: 0 -14px; }

.AssetsList-item {
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  width: 88px;
  min-width: 88px;
  height: 56px;
  margin-bottom: 16px;
  margin-left: 16px;
  overflow: hidden;
  border: 1px solid rgba(46, 60, 67, 0.08);
  border-radius: 4px;
  background: #F9F9F9; }
  .AssetsList-item:last-child {
    margin-right: 0; }
  .AssetsList-item:hover {
    border: 1px solid #DDD;
    cursor: pointer; }

.AssetsList-item.AssetsList-item--text {
  border: 1px dotted #DDD; }
  .AssetsList-item.AssetsList-item--text:hover {
    border: 1px solid #DDD;
    cursor: pointer; }

.AssetsList-item.AssetsList-item--text.is-selected {
  border: 1px solid rgba(46, 60, 67, 0.08); }

.AssetsList-item.is-selected {
  border: 2px solid #1785FB; }

.AssetsList-item--small {
  width: 40px;
  min-width: 40px;
  height: 40px; }

.AssetsList-item--medium {
  width: 56px;
  min-width: 56px;
  height: 56px; }
