.IconActions {
  height: 17px; }

.IconPicker {
  margin-bottom: 20px; }

.IconList {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  margin: 0 -12px 0 0; }

.IconList-item {
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  width: 88px;
  min-width: 88px;
  height: 56px;
  margin-right: 10px;
  margin-bottom: 10px;
  overflow: hidden;
  border: 1px solid #cccccc;
  border-radius: 4px;
  background: white; }
  .IconList-item:last-child {
    margin-right: 0; }
  .IconList-item:hover {
    border: 1px solid #AAA;
    cursor: pointer; }

.IconList-item--small {
  width: 40px;
  min-width: 40px;
  height: 40px; }

.IconList-item--dashed {
  border-style: dashed; }
  .IconList-item--dashed:hover {
    border: 1px dashed #AAA; }

.IconList-item.is-selected {
  border: 1px solid #1181FB; }

.IconItem-icon {
  vertical-align: middle; }

.IconText.is--critical {
  color: #FF6C6C; }

.is-hidden {
  display: none; }

.IconModal {
  padding: 0 !important; }
